  color: #fff;
  font-weight: 700;
  display: block;
  position: relative;
  padding-bottom: 5px;
  padding-top: 10px;
}

.main-navigation ul.menu > li > a::after {
  content: "";
  width: 0;
  height: 3px;
  left: 50%;
  bottom: 0;
  position: absolute;
  background: #EB5F00;
  transition: all 0.3s ease;
}

