  color: #EB5F00;
}

.main-navigation ul.menu > li.current-menu-item > a::after {
  width: 100%;
  left: 0;
}

.main-navigation ul.menu > li:hover ul {
  opacity: 1;
  visibility: visible;
}

.main-navigation ul.menu > li:hover > a {
  color: #EB5F00;
}

.main-navigation ul.menu > li:hover > a::after {
  width: 100%;
  left: 0;
}

