.main-navigation ul.menu::after {
  content: "";
  display: table;
  clear: both;
}

.main-navigation ul.menu ul {
  position: absolute;
  left: 0;
  top: 100%;
  width: 200px;
  background: #fff;
  padding: 0;
  border-radius: 6px;
